<template>
  <div class="zkl_container">
    <!-- 搜索 -->
    <van-nav-bar title="特色课">
      <template #right>
        <van-icon name="search" size="26" color="grey" />
      </template>
    </van-nav-bar>
    <!-- 二级导航 -->
    <van-dropdown-menu>
      <van-dropdown-item title="分类">
        <div class="zkl_title_headjj">
          <div class="zkl_text">年级</div>
          <ul>
            <li
              v-for="(item, index) in classa"
              :key="index"
              @click="fenlei(item.name)"
            >
              {{ item.name }}
            </li>
          </ul>
        </div>
        <div class="zkl_title_body">
          <div class="zkl_text">学科</div>
          <ul>
            <li v-for="(item, index) in subject" :key="index" > 
              {{ item.name }}
            </li>
          </ul>
        </div>
        <div class="title-foot">
          <button class="butn1">重置</button>
          <button class="butn2">确定</button>
        </div>
      </van-dropdown-item>
      <van-dropdown-item title="排序">
        <div class="zkl_paixu_container">
          <div
            class="zkl_paixu_title"
            v-for="(item, index) in option2"
            :key="index"
          >
            {{ item.text }}
          </div>
        </div>
      </van-dropdown-item>
      <van-dropdown-item title="筛选">
        <div class="zkl_shaixuan_container">
          <ul>
            <li v-for="(item, index) in screen" :key="index">
              {{ item.name }}
            </li>
          </ul>
        </div>
      </van-dropdown-item>
    </van-dropdown-menu>
    <div class="zkl_shuju_container">
      <van-list
        class="zkl_shuju_bo"
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <div v-for="(item, index) in list" :key="index" class="zkl_xuanran">
          <div class="zkl_xinxi">
            <img :src="item.cover_img" alt="" />
            <span>{{ item.title }}</span>
          </div>
          <div class="zkl_baoming">
            <span>{{ item.sales_num }}人已报名</span>
            
            <div @click="search(item.id)">免费</div>
          </div>
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
import { characteristic, solt } from "../util/http";
export default {
  name: "",
  components: {},
  props: [],
  data() {
    return {
      classa: [
        { name: "初一" },
        { name: "初二" },
        { name: "初三" },
        { name: "高一" },
        { name: "高二" },
      ],
      subject: [
        { name: "语文" },
        { name: "数学" },
        { name: "英语" },
        { name: "物理" },
        { name: "化学" },
      ],
      option2: [
        { text: "综合排序", value: "a" },
        { text: "最新", value: "b" },
        { text: "最热", value: "c" },
        { text: "价格从低到高", value: "c" },
        { text: "价格从高到底", value: "c" },
      ],
      screen: [
        { name: "全部" },
        { name: "大班课" },
        { name: "小班课" },
        { name: "公开课" },
        { name: "点播课" },
        { name: "面授课" },
        { name: "音频课" },
        { name: "系统课" },
        { name: "图文课" },
        { name: "会员课" },
      ],
      list: [],
      loading: false,
      finished: true,
      total: 0,
      params: {
        page: 1,
        order_bay: 0,
        course_type: 0,
        classify_id: 0,
      },
      classification: [],
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {
    characteristic().then((res) => {
      console.log(res);
      this.list = res.list;
      this.finished = false;
      this.total = res.total;
    });
    solt().then((res) => {
      // console.log(res);
      this.classification = res.list;
    });
  },
  methods: {
    onLoad() {
      this.params.page++;
      characteristic({ page: this.params.page }).then((res) => {
        // console.log(res);
        const { current_page, list, last_page } = res;
        this.loading = false;
        
        // 数据全部加载完成
        if (this.list.length >= this.total) {
          this.finished = true;
        }

        this.list.last_page = last_page;
        this.list.current_page = current_page;
        // console.log(this.list.current_page);
        this.list = [...this.list, ...list];
      });
    },
    //分类
    fenlei(val) {
      var list = null;
      this.classification.forEach((item) => {
        // console.log(item);
        if (item.title == val) {
          item = this.list;
        }
      });
      this.list = this.list;
    },
  },
};
</script>

<style lang='scss' scoped>
