  align-items: center;
  .butn1 {
    width: 46%;
    height: 36px;
    font-size: 14px;
    border: #f1f1f1 1px solid;
    outline: none;
    background-color: white;
    border-radius: 6px;
  }
  .butn2 {
    width: 46%;
    height: 36px;
    font-size: 14px;
    border: none;
    outline: none;
    color: white;
    background-color: #eb6100;
